#navbar {
  overflow: hidden;
  background-color: #218009;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0 10px;
}

#navbar a:hover {
  background-color: #3ee714;
}

#navbar a {
  float: left;
  display: block;
  color: #eeeeee;
  text-align: center;
  padding: 15px 18px;
  text-decoration: none;
  font-size: 18px;
}

#main-content {
  padding: 60px 10px;
}

#main-footer {
  overflow: hidden;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #218009;
  padding: 0 10px;
  color: white;
  text-align: center;
}

#main-footer a:visited {
  color: silver;
}
